/* new design */

.data_entry {
  width: 100%;
  background-color: rgba(173, 170, 149, 0.603);
  padding: 20px 0;
}
.basic_data_entry,
.data_entry_property_image,
.dealer_data_entry,
.allData_entry {
  max-width: 750px;
  background-color: rgb(247, 247, 247);
  margin: auto;
  padding: 20px;
  margin-top: 20px;
}
.basic_data_entry_container h2,
.data_entry_property_image_container h2,
.allData_entry_container h2,
.dealer_data_entry_container h2 {
  text-align: center;
  padding-bottom: 15px;
}

.basic_data_entry_flex,
.allData_entry_flex,
.dealer_data_entry_flex {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.basic_data_entry_name {
  width: calc(100% - 23px);
  padding: 10px;
  font-size: 18px;
  outline: none;
  /* margin: 10px; */
}

.data_selection1 input,
.data_selection2 input,
.data_selection2 select,
.dealer_data_entry_selection1 input,
.dealer_data_entry_selection1 select,
.dealer_data_entry_selection2 input {
  padding: 10px;
  width: 300px;
  outline: none;
  margin-top: 15px;
}
.data_selection2 select,
.dealer_data_entry_selection1 select {
  width: 324px;
  border: 1px solid rgba(0, 0, 0, 0.525);
}
.data_selection2 textarea {
  width: 303px;
  padding: 10px;
  outline: none;
  margin-top: 15px;
}
textarea {
  resize: none;
  font-size: 15px;
}

.allData_entry_selection1 select,
.allData_entry_selection2 select {
  width: 324px;
  border: 1px solid rgba(0, 0, 0, 0.525);
  padding: 10px;
  margin-top: 15px;
}
.allData_entry_selection1 input {
  padding: 10px;
  width: 300px;
  outline: none;
  margin-top: 15px;
}
.allData_entry_highlight1,
.allData_entry_highlight2,
.allData_entry_highlight3 {
  width: calc(100% - 23px);
  padding: 10px;
  font-size: 18px;
  outline: none;
  margin-top: 15px;
}

.allData_entry_container textarea,
.data_entry_property_image_container input,
.dealer_data_entry_container textarea {
  width: calc(100% - 22px);
  font-size: 18px;
  outline: none;
  margin-top: 15px;
  padding: 10px;
}
.data_entry_property_image_container input {
  border: 1px solid rgba(0, 0, 0, 0.525);
}
.data_entry_map {
  max-width: 750px;
  background-color: rgb(247, 247, 247);
  margin: auto;
  padding: 20px;
  margin-top: 20px;
}
.Submit_button {
  text-align: center;
  margin-top: 15px;
}
.Submit_button button {
  padding: 15px 30px;
  font-size: 20px;
  border-radius: 10px;
  border: none;
  background-color: rgb(51, 47, 47);
  color: white;
  cursor: pointer;
  transition: all 0.5s;
}
.Submit_button button:hover {
  transform: scale(1.05);
  background-color: black;
}
