.About {
  width: 100%;

  background-color: rgb(255, 255, 255);
}
.About h2 {
  text-align: center;
  padding: 50px 0;
  font-size: 40px;
}
.About_rapper {
  max-width: 800px;
  display: flex;
  flex-direction: row;
  margin: auto;
  justify-content: space-between;
  padding: 60px 0;
}
.About_rapper1 {
  max-width: 800px;
  display: flex;
  flex-direction: row;
  margin: auto;
  justify-content: space-between;
  padding: 60px 0;
}
/* .About_img {
  position: absolute;
  top: 170px;
  left: 9%;
  animation-duration: 2s;
  animation-name: textleft;
  z-index: 2;
} */
.About_img img {
  width: 350px;
}

.About_img1 img {
  width: 350px;
}
.About_detail {
  width: 300px;
}
.About_detail span {
  font-size: 20px;
  font-weight: 500;
}
.About_detail1 {
  width: 300px;
}
.About_detail1 span {
  font-size: 20px;
  font-weight: 500;
}
@media (max-width: 850px) {
  .About_rapper {
    max-width: 720px;
  }
  .About_rapper1 {
    max-width: 720px;
  }
}
@media (max-width: 750px) {
  .About_rapper {
    max-width: 720px;
    display: flex;
    flex-direction: column;
    margin: auto;
    justify-content: space-between;
    padding: 20px;
  }
  .About_rapper1 {
    max-width: 720px;
    display: flex;
    flex-direction: column-reverse;
    margin: auto;
    justify-content: space-between;
    padding: 0;
  }
  .About_img,
  .About_detail1,
  .About_detail,
  .About_img1 {
    margin: auto;
    padding: 10px 0;
  }
}
@media (max-width: 400px) {
  .About_img img,
  .About_detail1,
  .About_detail,
  .About_img1 img {
    width: 280px;
  }
}
