* {
  scroll-behavior: smooth;
}
.Content {
  background-size: cover;
  width: 100%;
  height: 100vh;
}
.Content_carousel {
  position: absolute;
  z-index: -10;
}
.Img_slider img {
  width: 100%;
  height: 100vh;
  object-fit: cover;
}
.Bg_text {
  width: 100%;
  height: calc(100vh);
  background-color: rgba(0, 0, 0, 0.5);
}
.Maintext {
  color: white;
  position: absolute;
  top: 210px;
  /* left: 240px; */
  left: 22%;
  animation-duration: 2s;
  animation-name: textup;
  z-index: 2;
}
.Maintext p {
  color: white;
  /* position: absolute; */
  /* top: 200px;
  left: 90px; */

  animation-duration: 2s;
  animation-name: textup;
  z-index: 2;
  font-size: 30px;
  font-family: "Libre Baskerville", serif;
}
.text {
  font-size: 25px;
  color: rgb(223, 167, 12);
  font-family: "Girassol", cursive;
}
.Home0 {
  font-size: 26px;
  padding: 0 3px;
}
.Home1 {
  font-size: 22px;
  padding: 0 3px;
}
.Home2 {
  font-size: 16px;
  padding: 0 3px;
}
.Home3 {
  font-size: 8px;
  padding: 0 3px;
}

@keyframes textup {
  from {
    opacity: 0;
    transform: translate3d(0, -200px, 0);
  }
  to {
    opacity: 1;
    transform: translate3d(0, 0px, 0);
  }
}
@media (max-width: 980px) {
  .Maintext {
    left: 30%;
  }
}
@media (max-width: 835px) {
  .text {
    font-size: 20px;
  }
  .Maintext p {
    font-size: 20px;
  }
}
@media (max-width: 700px) {
  .Maintext {
    /* left: 180px; */
  }
}
@media (max-width: 530px) {
  .Maintext {
    left: 20%;
  }
}
@media (max-width: 412px) {
  .Maintext {
    left: 10%;
    top: 45%;
    font-size: 14px;
  }
  .text {
    font-size: 18px;
  }
}
