.Property_img img {
  width: 100%;
}
.Property_description {
  display: flex;
  justify-content: space-between;
  margin: 40px;
}

.Property_review p {
  padding: 15px;
  background-color: rgb(204, 169, 12);
  border: 1px solid rgb(161, 126, 8);
  border-radius: 5px;
  font-weight: bold;
  cursor: pointer;
}
.Property_review p:hover {
  transform: translateY(-3px);
}

.Property_value {
  display: flex;
  justify-content: space-between;
  margin: 40px;
}
.Property_contact p {
  padding: 15px;
  background-color: rgb(204, 12, 12);
  color: white;
  border-radius: 5px;
  font-weight: bold;
  font-size: 14px;
  cursor: pointer;
}
.Property_contact p:hover {
  transform: translateY(-3px);
}
.Property_details hr {
  width: 90%;
  margin: auto;
}
.Property_about {
  margin: 40px;
}
.Property_about p {
  font-size: 14px;
}
.Property_about h2 {
  padding-bottom: 8px;
}
.Property_alldetail {
  display: flex;
  justify-content: space-around;
  margin: 40px;
}
.Property_alldetail h6 {
  padding-bottom: 7px;
}
.Property_alldetail p {
  font-size: 12px;
}
.Property_alldetail_rapper1 {
  display: flex;
  flex: 1;
  justify-content: space-between;
}
.Property_Status {
  flex: 1;
  margin-top: 10px;
}
.Property_launch {
  flex: 1;
  margin-top: 10px;
}
.Property_alldetail_rapper2 {
  display: flex;
  flex: 1;
  justify-content: space-between;
}
.Property_prossession {
  flex: 1;
  margin-top: 10px;
}
.Property_Units {
  flex: 1;
  margin-top: 10px;
}
.Property_disclaimer {
  margin: 40px;
}
.Property_disclaimer p {
  font-size: 14px;
}
.Property_highlights {
  margin: 40px;
}
.Property_highlights p {
  font-size: 14px;
}
.Property_highlights h2 {
  padding-bottom: 8px;
}
.Property_highlights p i {
  color: chartreuse;
}
.Property_Amenities {
  margin: 40px;
}
.Property_Specifications {
  margin: 40px;
  border: 1px solid black;
  border-radius: 5px;
  padding: 20px;
}
.Property_Specifications h2 {
  padding-bottom: 8px;
}

.Property_Specifications p {
  font-size: 14px;
  margin-top: 5px;
}
.Property_Locality {
  margin: 40px;
}
.Property_Locality h2 {
  padding-bottom: 13px;
}
.Property_Locality h4 {
  padding-bottom: 10px;
}
.Property_Locality p {
  padding-bottom: 5px;
}
.Property_Locality p i {
  color: chartreuse;
}
.Property_trend {
  margin: 30px;
}
.Property_trend h2 {
  padding-bottom: 10px;
}
@media (max-width: 500px) {
  .Property_alldetail {
    flex-direction: column;
  }
}
