.Property_detail_card {
  width: 350px;
  background-color: white;
  padding: 5px;
  border-radius: 5px;
  margin: auto;
  /* margin-bottom: 20px; */
  margin-top: 10px;
}
.Property_detail_card_rapper {
  display: flex;
  margin-bottom: 10px;
  margin: auto;
}
.Property_detail_card_label {
  flex: 1;
}
.Review_details {
  flex: 1;
  overflow: hidden;
}
.Property_detail_card button {
  padding: 6px;
  width: 100%;
  background-color: rgba(201, 22, 22, 0.877);
  color: white;
  font-size: 18px;
  font-weight: 900;
  border: none;
  outline: none;
  border-radius: 5px;
  transition: all 0.2s;
}
.Property_detail_card button:hover {
  background-color: red;
  transform: translateY(-3px);
}
.column-2 {
  flex: 0 0 50%;
  margin: auto;
}
