.Admin_drawer {
  position: absolute;
  width: 200px;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.904);
  color: white;
  z-index: 3;
}
.Admin_drawer_icon {
  margin-bottom: 10px;
}
.Admin_drawer_icon i {
  font-size: 25px;
  padding-left: 178px;
}
.Admin_drawer p {
  padding: 15px;
  font-size: 20px;
  font-weight: 900;
  transition: all 0.2s;
}
.Admin_drawer p:hover {
  background-color: rgb(139, 18, 18);
}
