.Search {
  background-color: rgb(255, 255, 255);
  border-radius: 20px;
  border: 1px solid black;
}

.Searchbar {
  display: flex;
  flex-direction: row;
  outline: none;
  padding: 4px;
  margin: 0;
}
.three_filter {
  display: flex;
  flex-direction: row;
  padding-right: 5px;
}

select {
  outline: none;
  padding: 8px 2px;
  border: none;
}

.Search_detail i {
  color: black;
  padding-left: 7px;
}

.Search_detail3 i {
  color: black;
  padding-left: 6px;
}
.Search_location i {
  color: black;
  padding-left: 4px;
}
.Search_location {
  padding-right: 5px;
}
.Search_location input {
  outline: none;
  padding: 8px 10px;
  width: 80%;
  border: none;
}
.Search_button button {
  padding: 8px 10px;
  width: 100%;
  border-radius: 15px;
  color: black;
  border: none;
  background-color: rgb(206, 163, 44);
  transition: 0.3s;
}
.Search_button button:hover {
  color: rgb(206, 163, 44);
  background-color: black;
}
.Search_button button i {
  padding-right: 5px;
}
@media (max-width: 920px) {
  .Search {
    display: inline-block;
  }
  .Searchbar {
    flex-direction: column;
  }
}
