.User_card {
  width: 300px;
  background-color: white;
  padding: 10px;
  border-radius: 5px;
  margin: auto;
  margin-top: 15px;
  margin-bottom: 15px;
}
.User_card_rapper {
  display: flex;

  margin-bottom: 5px;
}
.User_card_label {
  flex: 4;
}
.User_card_details {
  flex: 5;
}

.User_column2 {
  flex: 0 0 50%;
}
.approve {
  color: green;
  font-size: 20px;
  font-weight: bold;
  text-align: center;
}
.reject {
  color: rgb(128, 0, 0);
  font-size: 20px;
  font-weight: bold;
  text-align: center;
}
.sold_Out {
  color: rgb(0, 12, 107);
  font-size: 20px;
  font-weight: bold;
  text-align: center;
}

