/* style to card2 */
.Card_img img {
  width: 300px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
.Card_detail {
  padding: 3px;
}
.col-3 {
  flex: 0 0 33.33%;
  margin-bottom: 25px;
}
.col-3 a {
  color: black;
}
@media (max-width: 980px) {
  .col-3 {
    flex: 0 0 50%;
  }
}
@media (max-width: 700px) {
  .col-3 {
    flex: 0 0 100%;
  }
}
@media (max-width: 460px) {
  .col-3 {
    flex: 0 0 100%;
  }
}
