/* nav css */
.Navbar {
  display: flex;
  justify-content: space-between;
  background-color: rgb(25, 25, 25);
}

.Navlogo img {
  height: 54px;
}

.Navdetail {
  display: flex;
}

.icon {
  color: white;
  font-size: 25px;
  padding: 20px;
  float: right;
  cursor: pointer;
  display: none;
  background: none;
}
@media (max-width: 1048px) {
}
@media (max-width: 909px) {
  .icon {
    display: block;
  }
  .Navdetail {
    display: none;
  }
  .Navdetail a {
    width: 100%;
  }

  .Navdetail Link {
    width: 100%;
  }

  .Navlogo img {
    width: 80px;
    height: 70px;
  }
}

#hidden {
  position: absolute;
  width: 100%;
  top: 80px;
  left: 0;
  text-align: center;
  flex-direction: column;
  display: block;
  background: rgba(0, 0, 0, 0.7);
  z-index: 4;
}

.Navdetail p {
  padding: 20px;
  font-weight: 700;
  color: white;
  transition: all 0.5s ease;
}
.Navdetail p:hover {
  background-color: rgb(158, 22, 22);
}
