.help_desk {
  width: 100%;
  /* height: 110vh; */
}
.help_desk_rapper {
  max-width: 1000px;
  display: flex;
  flex-direction: row;
  margin: auto;
  justify-content: space-between;
  padding: 60px 0;
}
.help_desk_video {
  width: 400px;
  padding: 20px;
  border-right: 1px solid black;
  text-align: center;
}
.help_desk_video h2,
.help_desk_member h2,
.help_desk_video p,
.help_desk_member p {
  padding-bottom: 10px;
}
.help_desk_member {
  width: 400px;
  padding: 20px;
  text-align: center;
}
.help_desk_video_card {
  width: 300px;
  height: 150px;
  background-color: chartreuse;
  margin: auto;
}
.ContactUs {
  text-align: center;
  width: 100%;
  height: 200px;
  margin: 30px 0;
}
.ContactUs button {
  padding: 10px 20px;
  border: none;
  color: white;
  background-color: blue;
  border-radius: 18px;
  margin: 10px;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.5s ease;
}
.ContactUs button:hover {
  transform: translateY(-5px);
}
