.admin_Filter {
  background-color: rgb(250, 200, 134);
  width: 100%;
}

.admin_Filter_rapper {
  display: flex;
  justify-content: space-around;
  padding: 10px 0;
  /* margin: auto; */
  flex-direction: column;
}
.admin_Filter_rapper1,
.admin_Filter_rapper2 {
  display: flex;
  width: 800px;
  margin: auto;
}
.admin_FilterProperty_Role {
  margin: 5px;
  flex: 1;
}
.admin_Filter_Name {
  margin: 5px;
  flex: 1;
}

.admin_Filter_email {
  margin: 5px;
  flex: 1;
}

.admin_Filter_phone {
  margin: 5px;
  flex: 1;
}
.admin_Filter_detail1 select {
  width: 170px;
  padding: 9px 10px;
}
.admin_Filter_location {
}
.admin_Filter_location input {
  outline: none;
  padding: 10px;
  width: 150px;
  border: none;
}
.admin_Filter_name {
}

.admin_Filter_name input,
.admin_Filter_phone input {
  outline: none;
  padding: 10px;
  width: 100%;
  border: none;
}
.admin_Filter_Button {
  margin: auto;
}
.admin_Filter_Button button {
  padding: 10px;

  color: black;
  border: none;
  background-color: rgb(206, 163, 44);
  transition: 0.3s;
}
.admin_Filter_button button:hover {
  background-color: burlywood;
}
.admin_Filter_button_button button i {
  padding-right: 5px;
}

/* user details */

.admin_FilterProperty_Role select {
  outline: none;
  padding: 9px;
  width: 100%;
  border: none;
}

.admin_Filter_Name input,
.admin_Filter_phone input,
.admin_Filter_email input {
  outline: none;
  padding: 10px;
  width: calc(100% - 20px);
  border: none;
}
.admin_Filter_Button button {
  padding: 10px;

  color: black;
  border: none;
  background-color: rgb(206, 163, 44);
  transition: 0.3s;
}
.admin_Filter_Button button:hover {
  background-color: burlywood;
}
@media (max-width: 830px) {
  .admin_Filter_rapper1,
  .admin_Filter_rapper2 {
    width: 700px;
  }
}
@media (max-width: 710px) {
  .admin_Filter_rapper1,
  .admin_Filter_rapper2 {
    width: 500px;
  }
}
@media (max-width: 710px) {
  .admin_Filter_rapper1,
  .admin_Filter_rapper2 {
    flex-direction: column;
    width: 310px;
  }
}
