.admin_Filter {
  /* display: ; */
  background-color: rgb(250, 200, 134);
  width: 100%;
}

.admin_property_Filter_rapper {
  display: flex;
  width: 800px;
  flex-direction: row;
  justify-content: space-around;
  margin: auto;
  padding: 10px 0;
}
.Admin_property_Filter_rapper1 {
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  margin: auto;
}
.admin_property_Filter_detail1,
.admin_property_Filter_location,
.admin_property_Filter_Name {
  flex: 1;
  margin: 5px;
}
.admin_property_Filter_detail1 select {
  width: 100%;
  /* padding: 9px 10px; */
}

.admin_property_Filter_location input {
  outline: none;
  padding: 10px;
  width: calc(100% - 20px);
  border: none;
}
.Admin_property_Filter_rapper2 {
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  margin: auto;
}
.admin_property_Filter_Name input {
  outline: none;
  padding: 10px;
  width: calc(100% - 20px);
  border: none;
}

.admin_Filter_button {
  margin: auto;
}
.admin_Filter_button button {
  padding: 10px;
  color: black;
  border: none;
  background-color: rgb(206, 163, 44);
  transition: 0.3s;
  margin-top: 20px;
}
/* .admin_Filter_button button:hover {
  background-color: burlywood;
} */

.admin_Filter_Button button {
  padding: 10px;

  color: black;
  border: none;
  background-color: rgb(206, 163, 44);
  transition: 0.3s;
}
.admin_Filter_Button button:hover {
  background-color: burlywood;
}
@media (max-width: 720px) {
  .admin_property_Filter_rapper {
    flex-direction: column;
    width: 450px;
  }
}
@media (max-width: 500px) {
  .admin_property_Filter_rapper {
    flex-direction: column;
    width: 300px;
  }
}
