.forgot_password {
  margin: 0 0 20px 0;
  color: #474444;
  cursor: pointer;
}
.forgot_password:hover {
  text-decoration: underline;
}

input[type="submit"] {
  width: 100px;
  text-align: center;
  padding-top: 10px;
  padding-bottom: 10px;
  border-radius: 25px;
  transition: all 0.3s ease;
  border: 1px solid;
  background: #2691d9;
  color: aliceblue;
  cursor: pointer;
  font-weight: 900;
  outline: none;
}
input[type="submit"]:hover {
  background: orange;
}

.Login {
  background-color: rgba(143, 140, 135, 0.918);
  width: 100%;
  height: calc(100vh - 61px);
}

.login_detail {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 600px;
  display: flex;
  flex-direction: row;
  box-shadow: 5px 5px 5px rgba(78, 76, 74, 0.493);
}
.Log_img {
  background-image: url(../../img/hero-bg.jpg);
  background-size: cover;
  flex: 1;
}
.log_info {
  flex: 1;
  background-color: white;
  padding: 20px;
}
.log_info input {
  border: none;
}
.log_info h2 {
  padding-top: 10px;
  padding-bottom: 10px;
}
.log_info p {
  padding-bottom: 25px;
}
.signup_link {
  margin: 20px 0;
  text-align: center;
  font-size: 16px;
  color: rgba(102, 102, 102, 0.726);
}
.signup_link a {
  color: #2691d9;
  text-decoration: none;
}
.signup_link a:hover {
  text-decoration: underline;
}
.txt_field i {
  color: #4744448e;
}
.txt_field {
  padding-top: 5px;
  padding-bottom: 5px;
}

.txt_field input {
  outline: none;
  padding: 7px 5px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.322);
  font-size: 16px;
  width: 80%;
  background: none;
}
.log_button {
  margin-left: 30%;
  margin-top: 20px;
}

@media (max-width: 700px) {
  .login_detail {
    width: 550px;
  }
  /* .log_info {
    flex: 2;
  } */
}
@media (max-width: 575px) {
  .login_detail {
    width: 500px;
  }
  /* .log_info {
    flex: 2;
  } */
}
@media (max-width: 540px) {
  .login_detail {
    width: 400px;
  }
  .Log_img {
    flex: 0;
  }
}
@media (max-width: 460px) {
  .login_detail {
    width: 300px;
  }
  /* .log_info {
    flex: 2;
  } */
}
