.service_card {
  width: 180px;
  margin: 30px;
  background-color: rgb(223, 167, 12);
  color: black;
  padding: 25px;
  border-radius: 5px;
}
.service_card:hover {
  /* transform: translateY(-5px); */
  transform: scale(1.03);
  box-shadow: 0 8px 50px rgba(0, 0, 0, 0.2);
}
.service_card p {
  font-size: 15px;
  font-weight: 900;
  font-family: "Girassol", cursive;
}
