.AdminCard {
  flex: 0 0 33.33%;
  padding-top: 20px;
  /* margin: auto; */
}
.admin_card {
  /* margin: auto; */
  width: 300px;
  background-color: white;
  padding: 8px;
  border-radius: 5px;
}
.admin_card p {
  font-size: 15px;
  padding: 2px;
}
.Admincard_button button {
  padding: 5px;
  margin-top: 5px;
  width: 100%;
  border-radius: 5px;
  outline: none;
  border: none;
  cursor: pointer;
  font-weight: 800;
  color: white;
  background-color: rgba(201, 22, 22, 0.877);
  transition: all 0.2s;
}
.Admincard_button button:hover {
  background-color: red;
  transform: translateY(-3px);
}
