.contact {
  /* background-color: rgb(255, 255, 255); */
  max-width: 100%;
  height: calc(100vh - 65px);
}
.contact_polygon {
  width: 100%;
  height: calc(100vh - 65px);
  position: absolute;
  clip-path: polygon(100% 0, 0% 100%, 100% 100%);
  background-color: rgb(174, 223, 238);
  z-index: -5;
}
.contact_rapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  max-width: 800px;
  margin: auto;
  padding-top: 80px;
}
.contact_text {
  width: 300px;
  text-align: center;
  padding-top: 130px;
  /* margin: auto; */
}
.feedback_form {
  width: 350px;
  border: 1px solid blue;
  /* margin: auto; */
  background-color: white;
}
.feedback_form h3,
.feedback_form p {
  font-size: 20px;
  text-align: center;
  padding: 10px;
}
.feedback_form_rapper {
  display: flex;
  flex-direction: column;
}
.ContactName input,
.ContactEmail input,
.ContactFeedback textarea {
  padding: 10px;
  width: calc(100% - 55px);
  margin: 10px 0 10px 15px;
  font-size: 15px;
  outline: none;
}
.ContactFeedback textarea {
  height: 100px;
  /* resize: vertical; */
}
.Contact_button {
  width: 100%;
  text-align: center;
}
.Contact_button p {
  /* background-color: rgb(68, 68, 247); */
  padding: 10px;
  color: rgb(68, 68, 247);
  font-weight: 700;
  font-size: 20px;
  margin: 10px 27%;
  border: 2px solid rgb(68, 68, 247);
  border-radius: 20px;
}
.Contact_button p:hover {
  background-color: rgb(68, 68, 247);
  padding: 10px;
  color: white;
  margin: 10px 27%;
  border-radius: 20px;
}
@media (max-width: 870px) {
  .contact_rapper {
    flex-direction: column;
    padding-top: 20px;
  }
  .contact_text {
    padding-top: 0px;

    margin: auto;
  }
  .feedback_form {
    margin: auto;
  }
  .contact_polygon {
    height: 120vh;
  }
}
@media (max-width: 420px) {
  .feedback_form {
    border: none;
    background-color: rgba(252, 252, 252, 0);
  }
}
@media (max-width: 330px) {
  .feedback_form {
    width: 300px;
    border: none;
    background-color: rgba(252, 252, 252, 0);
  }
}
